import { withStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import PropTypes from "prop-types";
import React from "react";
import { Link } from "react-router-dom";
import TextLoop from "react-text-loop";
import BigButton from "../components/elements/BigButton";
import DarkTriangles from "../resources/images/trianglesDarkv2.svg";
import ParticlesBg from "particles-bg";

const styles = (theme) => ({
  "@global": {
    body: {
      backgroundColor: theme.palette.background.default,
      background: `url(${DarkTriangles}) no-repeat center center fixed`,
      backgroundSize: "cover",
      height: "100%",
    },
  },
  root: {
    ...theme.mixins.gutters(),
    display: "flex",
    flexGrow: 1,
    flexDirection: "column",
  },

  featuresBox: {
    paddingTop: theme.spacing(8),
    display: "flex",
    flex: "0 1 auto",
    flexDirection: "row",
    flexWrap: "nowrap",
  },

  cardBox: {
    ...theme.mixins.gutters(),
    display: "flex",
    flexDirection: "row",
    flexWrap: "wrap",
    justifyContent: "center",
  },

  cardItem: {
    margin: theme.spacing(1),
    flexBasis: "auto" /* default value */,
    flexGrow: 1,
  },

  cardMedia: {
    flexGrow: 1,
    paddingTop: "56.25%", // 16:9
  },

  cardContent: {
    flexGrow: 1,
  },

  icon: {
    flexGrow: 1,
  },

  heroUnit: {
    display: "flex",
    flexGrow: 1,
  },

  heroContent: {
    display: "flex",
    flexGrow: 1,
    flexDirection: "column",
    justifyContent: "flex-start",
    paddingLeft: "calc(1/16 * 100%)",
    padding: `${theme.spacing(8)}px 0 ${theme.spacing(6)}px`,
  },

  buttonBox: {
    display: "flex",
    flexGrow: 1,
    paddingTop: theme.spacing(8),
  },

  footer: {
    flexGrow: 1,
  },
  titleLoop: {
    color: theme.palette.secondary.main,
  },
});

function Album(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <main>
        {/* Hero unit */}
        <div className={classes.heroUnit}>
          <div className={classes.heroContent}>
            <Typography variant="h2">
              {"Play "}
              <span className={classes.titleLoop}>
                <TextLoop
                  springConfig={{ stiffness: 340, damping: 30 }}
                  speed={2000}
                >
                  <>Quality</>
                  <>Exciting</>
                  <>Fun</>
                </TextLoop>
              </span>
            </Typography>
            <Typography variant="h2" color="textPrimary">
              Overwatch² games.
            </Typography>

            <div className={classes.buttonBox}>
              <BigButton
                variant="contained"
                size="large"
                color="primary"
                href="https://discord.gg/XfpSS92ceE"
              >
                Join the Queue
              </BigButton>
            </div>

            <Box sx={{ marginTop: 40 }}>
              <div>
                <Typography variant="h5" component="h2" gutterBottom>
                  {"Introducing Overqueue"}
                </Typography>
                <Typography variant="body1">
                  A new and alternative Overwatch role-queue skill-based
                  matchmaker, made for Overwatch².
                  {"\nDiscord-based, command-less."}
                </Typography>
              </div>
            </Box>
          </div>
        </div>
      </main>
      <div className={classes.featuresBox}></div>
      <ParticlesBg type="cobweb" bg={true} num={150} color={"#7e4d03"} />
    </div>
  );
}

Album.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Album);
