import AppBar from "@material-ui/core/AppBar";
import { withStyles } from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import React from "react";
import { Link, withRouter } from "react-router-dom";
import { Provider } from "unstated";

const styles = (theme) => ({
  root: {},
  headerText: {
    flexGrow: 1,
    fontSize: theme.spacing(3),
    lineHeight: `${theme.spacing(4)}px`, //24
    paddingLeft: `${theme.spacing(1)}px`, //8
    position: "relative",
    top: "-1.5px",
    verticalAlign: "middle",
    textTransform: "lowercase",
    textRendering: "optimizeLegibility",
  },

  headerOver: {
    color: theme.palette.secondary.main,
  },

  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
});

const Topbar = (props) => {
  const { classes } = props;

  const defaultButtons = <React.Fragment></React.Fragment>;

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Typography
            color="inherit"
            className={classes.headerText}
            variant="h6"
          >
            <Link
              to="/"
              style={{ textDecoration: "none", color: "inherit", outline: 0 }}
            >
              <span className={classes.headerOver}>Over</span>
              Queue
            </Link>
          </Typography>
          <Provider>{defaultButtons}</Provider>
        </Toolbar>
      </AppBar>
    </div>
  );
};

export default withRouter(withStyles(styles)(Topbar));
