import React, { Component } from "react";
import { Route } from "react-router-dom";
import PropTypes from "prop-types";
import { Provider, Subscribe } from "unstated";

class RouteToggle extends Component {
  render() {
    const { authed, unauthed, ...rest } = this.props;
    return (
        <Route
          {...rest}
          component={false ? authed : unauthed}
        />
    );
  }
}

RouteToggle.propTypes = {
  authed: PropTypes.func.isRequired,
  unauthed: PropTypes.func.isRequired
};

export default RouteToggle;
